import { defineComponent, h, ref } from 'vue'
import { useRouter } from 'vue-router'
import { useSuspense } from 'composable/components'
import { useCreate } from 'composable/buyback'
// import { useLabels } from 'composable/shipping'
import { useShippingCarrier } from 'composable/shipping'
import { usePaymentMethods, useCreate as useCreateCustomer } from 'composable/customer'
import { useUser } from 'composable/user'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Stepper from 'bbUi/components/stepper/stepper.vue'
import PaymentMethod from 'components/PaymentMethod.vue'
import Device from 'components/Device.vue'
import Customer from 'components/Customer'
import Breadcrumb from './Breadcrumb.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    PaymentMethod,
    Device
  },
  setup() {
    const { me } = useUser()
    const router = useRouter()
    const { genSuspense } = useSuspense()
    const step = ref(1)
    const { dataCustomer, dataPaymentMethod, dataDevice, loading, action: createBuyback } = useCreate('online')
    const { state: statePaymentMethods, action: actionPaymentMethods } = usePaymentMethods('online')
    const { data: newCustomerData, action: actionCreateCustomer, loading: loadingCustomer } = useCreateCustomer()
    const isNewCustomer = ref(false)
    const isPayoutSelected = ref(false)
    const refPaymentMethod = ref(false)
    const refCustomer = ref(false)

    const selectCustomer = async (customer) => {
      dataCustomer.customer_id = customer.customer_id || customer.id
      dataCustomer.customer_email = customer.email
      dataCustomer.customer_firstname = customer.first_name
      dataCustomer.customer_lastname = customer.last_name
      await actionPaymentMethods(customer.customer_id)
      step.value = 2
    }
    const setNewCustomer = (data) => {
      Object.assign(newCustomerData, data)
    }
    const registerNewCustomer = async () => {
      const isValid = refCustomer.value.refChildComponent.validate && refCustomer.value.refChildComponent.validate() || false
      if( isValid ){
        await actionCreateCustomer(async (newCustomer) => {
          await selectCustomer(newCustomer)
        })
      }
    }
    const genCustomerButton = () => {
      return h(Button, {
        color: 'teal',
        loading: loadingCustomer.value,
        onClick: () => registerNewCustomer()
        },{
          default: () => 'Continue'
      })
    }

    const genStepComponent = () => {
      if(step.value === 1) return [h(Customer, {
        onData: setNewCustomer,
        onChoose: selectCustomer,
        ref: refCustomer,
        choose: true,
        onChange: (tab) => isNewCustomer.value = tab === 'CustomerForm'
      }), h('div', {
        class: ['flex', 'items-center', 'justify-end']
      })]
      else {
        let genChild
        if(step.value === 2) genChild = h(PaymentMethod, {
          data: dataPaymentMethod.value,
          items: statePaymentMethods.value,
          ref: refPaymentMethod,
          autoSelect: isPayoutSelected.value,
          showTip: true,
          'onUpdate:data': data => {
            dataPaymentMethod.value = data
          }
        })
        else if(step.value === 3) genChild = genSuspense({
          component: Device,
          props: {
            data: dataDevice.value,
            'onUpdate:data': data => {
              dataDevice.value = data
            },
            buybackType: 'mailin'
          }
        })

        return [
          h(Card, {
            class: 'mb-4'
          }, {
            default: () => genChild
          })
        ]
      }
    }
    const genButtonPrevButton = () => {
      return h(Button, {
        view: 'outlined',
        color: 'teal',
        onClick: () => {
          if( step.value === 2 ) dataPaymentMethod.value = null
          step.value--
        }
      }, {
        default: () => 'Back'
      })
    }
    const genButtonStepBread = () => {
      if (step.value === 1) {
         return isNewCustomer.value && genCustomerButton() || ''
      }

      if (step.value === 2) {
        return h('div', {
          class: 'flex items-center justify-between gap-x-2'
        }, {
          default: () => [
            genButtonPrevButton(),
            h(
              Button,
              {
                color: 'teal',
                loading: loading.value,
                onClick: () => {
                  isPayoutSelected.value = true
                  const isValidated = (refPaymentMethod.value.refChildComponent.validate && refPaymentMethod.value.refChildComponent.validate()) ?? false
                  if( isValidated ) step.value++
                }
              },
              {
                default: () => 'Continue'
              }
            )
          ]
        })
      }

      if (step.value === 3) {
        return h('div', {
          class: 'flex items-center justify-between gap-x-2'
        }, {
          default: () => [
            genButtonPrevButton(),
            h(
              Button,
              {
                color: 'teal',
                loading: loading.value,
                onClick: async () => {
                  if( !dataDevice.value ){
                    ElMessage({
                      message: `Please add at least one item first`,
                      type: 'warning'
                    })
                    return false
                  }
                  dataDevice.value = dataDevice.value.map((item) => {
                    const isCustomProduct = item.device.is_custom == 1
                    if(isCustomProduct) return ({
                      category: item.device.category_name,
                      category_id: item.device.category_id,
                      product_name: item.product.name,
                      condition: item.device.condition.name,
                      condition_id: item.device.condition.id,
                      price: item.device.price,
                      qty: item.device.qty,
                      is_mac: item.device.is_mac,
                      imei_sn: item.device.imei_sn,
                      is_custom: 1,
                      notes: item.device.notes,
                      ...(item.device.custom_color ? ({
                        custom_color: item.device.custom_color,
                        color_id: null
                      }) : ({
                        color_id: item.device.color?.id || null
                      }))
                    })
                    return ({
                      brand: item.product.brand ? item.product.brand : item.product.name,
                      category: item.product.category ? item.product.category : item.product.name,
                      product_id: item.product.id,
                      condition_id: item.device.condition.id,
                      price: item.device.price,
                      qty: item.device.qty,
                      is_mac: item.device.is_mac,
                      imei_sn: item.device.imei_sn,
                      unique_id: `${new Date().getTime()}_${item.product.id}_${item.device.condition.id}`,
                      is_custom: 0,
                      notes: item.device.notes,
                      ...(item.device.golf_specs),
                      ...(item.device.custom_color ? ({
                        custom_color: item.device.custom_color,
                        color_id: null
                      }) : ({
                        color_id: item.device.color?.id || null
                      })),
											questions: item.device.questions.reduce((previousValue, currentValue) => {
												if (typeof currentValue.answer_id === 'object') {
													currentValue.answer.forEach((answer) => previousValue.push({
														question_id: currentValue.question_id,
														answer_id: answer.id
													}))
												}
												else if (!!currentValue.answer_id) previousValue.push({
													question_id: currentValue.question_id,
													answer_id: currentValue.answer.id
												})

												return previousValue
											}, [])
                    })
                  })

                  await createBuyback(response => {
                    router.push({
                      name: 'buybackOnlineDetail',
                      query: {
                        ref: 'create-order'
                      },
                      params: {
                        buybackId: response.data.box_id
                      }
                    })
                  })
                }
              },
              {
                default: () => 'Continue'
              }
            )
          ]
        })
      }

      return [
        h('span')
      ]
    }

    return () => [
      h(Breadcrumb, {}, {
        buttonStep: () => genButtonStepBread()
      }),
      h(Card, {}, {
        default: () => [
          h(Stepper, {
            activeStep: step.value,
            steps: ['Customer', 'Payout', 'Item'],
            class: 'mb-16',
            color: 'teal'
          }),
          genStepComponent()
        ]
      })
    ]
  }
})
